@charset "UTF-8";
:root {
  --font-family-base: "Noto Sans JP", sans-serif;
  --font-family-second: "Noto Serif JP", serif;
}

:root {
  --inner-padding: 25px;
}

:root {
  --font-weight-regular: 400;
  --font-weight-normal: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-extrabold: 800;
  --font-weight-black: 900;
}

:root {
  --black: #000;
  --white: #fff;
  --text-gray: #808080;
  --main-blue: #2E3192;
  --main-red: #c50018;
  --main-yellow: #FFE200;
  --bg-gray: #F2F2F2;
}

:root {
  --z-index-to-top: 200;
  --z-index-drawer: 1000;
  --z-index-header: 10000;
  --z-index-hamburger: 2000;
  --z-index-modal: 10000;
}

:root {
  --cq-ref-mobile: 375;
  --cq-ref: 1440;
  --fluid-basis-mobile: (100vw - 320px) / (375 - 320);
  --fluid-basis: (100vw - 768px) / (1440 - 768);
  --fluid-basis-2: (100vw - 1440px) / (1800 - 1440);
}

:root {
  --duration-short: 0.2s;
  --duration-base: 0.3s;
  --duration-long: 0.6s;
  --duration-fv: 0.7s;
  --ease-smooth-bounce: cubic-bezier(.48, .07, .33, .79);
}

body {
  color: var(--black);
  font-family: var(--font-family-base);
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.6vw;
  }
}
@media (min-width: 1000px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  overflow-wrap: anywhere;
  word-break: normal;
  line-break: strict;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "palt";
}

ul,
ol,
button,
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
time,
small {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

a,
span,
button {
  display: inline-block;
}

small,
time,
figure {
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
  border: 0;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
  transition: 0.3s;
}

button {
  color: inherit;
}

@media (any-hover: hover) {
  a:hover {
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  a[href*="tel:"] {
    pointer-events: none;
    cursor: default;
    text-decoration: none;
  }
}
a {
  -webkit-tap-highlight-color: transparent;
}

button {
  -webkit-tap-highlight-color: transparent;
}

.inner {
  width: 100%;
  padding-right: 25px;
  padding-left: 25px;
  margin-right: auto;
  margin-left: auto;
  max-width: 500px;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1070px;
    padding-right: 35px;
    padding-left: 35px;
  }
}

.not-found {
  margin-top: 3.125rem;
  padding-bottom: 3.5375rem;
}
@media screen and (min-width: 768px) {
  .not-found {
    margin-top: 5.3125rem;
    padding-bottom: 6.85rem;
  }
}

.not-found__inner {
  margin-inline: auto;
  max-width: 500px;
  padding-inline: var(--inner-padding);
}
@media screen and (min-width: 768px) {
  .not-found__inner {
    max-width: calc(902px + var(--inner-padding) * 2);
  }
}

.not-found__title {
  font-size: 1.25rem;
  font-weight: var(--font-weight-bold);
  line-height: 1.3;
  letter-spacing: 0.1em;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .not-found__title {
    font-size: 1.5625rem;
  }
}

.not-found__text {
  font-size: 0.9375rem;
  font-weight: var(--font-weight-semibold);
  line-height: 2;
  letter-spacing: 0.1em;
  text-align: center;
  margin-top: 1.4375rem;
}
@media screen and (min-width: 768px) {
  .not-found__text {
    font-size: 1rem;
    margin-top: 2.0625rem;
  }
}

.not-found__top-link-wrap {
  text-align: center;
  margin-top: 2.0625rem;
}
@media screen and (min-width: 768px) {
  .not-found__top-link-wrap {
    margin-top: 2.6875rem;
  }
}

.not-found__top-link-btn {
  background-color: var(--black);
  color: var(--white);
  padding: 0.9375rem 3.125rem;
  font-size: 0.90625rem;
  font-weight: var(--font-weight-bold);
  letter-spacing: 0.06em;
  transition: color var(--duration-base) ease;
  position: relative;
  transition: background-color var(--duration-base) ease;
}
@media screen and (min-width: 768px) {
  .not-found__top-link-btn {
    font-size: 1.125rem;
    padding: 1rem 4.5625rem;
  }
}
@media (any-hover: hover) {
  .not-found__top-link-btn:hover::after {
    transform: translateY(-50%) translateX(5px);
  }
  .not-found__top-link-btn:hover {
    background-color: var(--main-red);
  }
}

.entry {
  padding-block: 7.6875rem 2rem;
}
@media screen and (min-width: 768px) {
  .entry {
    padding-block: 11.625rem 5.5rem;
  }
}

.entry__inner {
  margin-inline: auto;
  padding-inline: var(--inner-padding);
  max-width: 500px;
}
@media screen and (min-width: 768px) {
  .entry__inner {
    max-width: calc(999px + var(--inner-padding) * 2);
  }
}

.entry__title {
  font-size: 1.125rem;
  font-weight: var(--font-weight-bold);
  letter-spacing: 0.12em;
  line-height: 1.7;
  font-feature-settings: initial;
}
@media screen and (min-width: 768px) {
  .entry__title {
    font-size: 2.5625rem;
    line-height: 1.77;
  }
}

.entry__lead {
  display: inline-block;
  color: var(--white);
  font-size: 0.875rem;
  font-weight: var(--font-weight-normal);
  letter-spacing: 0.05em;
  padding: 0.375rem 1.1875rem 0.375rem 1.1875rem;
  background-color: var(--main-red);
  margin-top: 1.375rem;
}
@media screen and (min-width: 768px) {
  .entry__lead {
    font-size: 1.375rem;
    letter-spacing: 0.04em;
    padding: 0.4375rem 1.1875rem;
    margin-top: 1.8125rem;
    line-height: 1.3;
  }
}

.entry__note {
  font-size: 0.625rem;
  font-weight: var(--font-weight-semibold);
  margin-top: 0.8125rem;
  letter-spacing: 0;
}
@media screen and (min-width: 768px) {
  .entry__note {
    font-size: 0.875rem;
    margin-top: 1.125rem;
    letter-spacing: 0.04em;
  }
}

.entry__content {
  margin-top: 2.125rem;
  background-color: #E5E5E5;
  border-radius: 15px;
  padding: 1.5625rem 0.625rem 1.875rem 1.25rem;
}
@media screen and (min-width: 768px) {
  .entry__content {
    margin-top: 2.5rem;
    border-radius: 20px;
    padding: 2.75rem clamp(34px, 34px + 40 * var(--fluid-basis), 74px) 3.625rem clamp(25px, 25px + 40 * var(--fluid-basis), 65px);
  }
}

.entry__schools {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.375rem;
  padding-bottom: 1rem;
  position: relative;
}
@media screen and (min-width: 768px) {
  .entry__schools {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.25rem;
    padding-bottom: 1.75rem;
  }
}

.entry__schools::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 97.6%;
  height: 1px;
  background-color: #000000;
}
@media screen and (min-width: 768px) {
  .entry__schools::before {
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .entry__school:nth-of-type(even) {
    margin-left: auto;
  }
}

.entry__school-name {
  font-family: var(--font-family-second);
  font-size: 2.5rem;
  font-weight: var(--font-weight-extrabold);
  letter-spacing: 0.06em;
  text-indent: -1.25rem;
  line-height: 1.28;
}
@media screen and (min-width: 768px) {
  .entry__school-name {
    text-indent: -1.4375rem;
  }
}

.entry__school-time {
  font-size: 1.75rem;
  font-family: var(--font-family-second);
  font-weight: var(--font-weight-extrabold);
  letter-spacing: 0.018em;
}
@media screen and (min-width: 768px) {
  .entry__school-time {
    font-size: 2.5rem;
  }
}

.entry__school-address {
  font-size: 0.75rem;
  font-weight: var(--font-weight-bold);
  margin-top: 0.375rem;
}
@media screen and (min-width: 768px) {
  .entry__school-address {
    font-size: 0.875rem;
    margin-top: 0.375rem;
  }
}

.entry__school-map {
  color: #0000FF;
  transition: color var(--duration-base) ease;
}
@media (any-hover: hover) {
  .entry__school-map:hover {
    color: #6161ff;
  }
}

.entry__school-attention {
  font-size: 0.75rem;
  font-weight: var(--font-weight-semibold);
  line-height: 1.4;
  text-indent: -0.75rem;
  padding-left: 0.9375rem;
}
@media screen and (min-width: 768px) {
  .entry__school-attention {
    font-size: 0.875rem;
    line-height: 1.2;
    text-indent: -0.875rem;
    padding-left: 0.875rem;
  }
}
@media screen and (min-width: 768px) {
  .entry__school-attention br.sp {
    display: none;
  }
}

.entry__cautions {
  margin-top: 0.6875rem;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
@media (max-width: 767px) {
  .entry__cautions {
    padding-inline: 0.5rem 0.375rem;
  }
}
@media screen and (min-width: 768px) {
  .entry__cautions {
    margin-top: 1.875rem;
  }
}

.entry__caution-item {
  font-size: 0.625rem;
  font-weight: var(--font-weight-bold);
  text-indent: -0.875rem;
  padding-left: 0.875rem;
  letter-spacing: 0.04em;
}
@media screen and (min-width: 768px) {
  .entry__caution-item {
    font-size: 0.875rem;
    text-indent: -1rem;
    padding-left: 1rem;
  }
}

.faq {
  padding-block: 6.6875rem 2.8125rem;
}
@media screen and (min-width: 768px) {
  .faq {
    padding-block: 13.5625rem 5.125rem;
  }
}

.faq__inner {
  margin-inline: auto;
  padding-inline: var(--inner-padding);
  max-width: 500px;
}
@media screen and (min-width: 768px) {
  .faq__inner {
    max-width: calc(1177px + var(--inner-padding) * 2);
  }
}

@media screen and (min-width: 768px) {
  .faq__content {
    display: grid;
    grid-template-columns: 101fr 915fr;
    gap: clamp(50px, 50px + 110 * var(--fluid-basis), 160px);
  }
}

.faq__title-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.0625rem;
}
@media screen and (min-width: 768px) {
  .faq__title-wrap {
    gap: 0.625rem;
  }
}

@media (max-width: 767px) {
  .faq__title {
    width: 4.1875rem;
  }
}

.faq__title-img {
  height: auto;
  object-fit: contain;
}

.faq__subtitle {
  font-size: 0.75rem;
  font-weight: var(--font-weight-semibold);
  letter-spacing: 0.06em;
}
@media screen and (min-width: 768px) {
  .faq__subtitle {
    font-size: 0.875rem;
  }
}

.faq__list {
  display: flex;
  flex-direction: column;
  gap: 1.4375rem;
}
@media (max-width: 767px) {
  .faq__list {
    margin-top: 1.75rem;
  }
}
@media screen and (min-width: 768px) {
  .faq__list {
    gap: 2rem;
  }
}

.faq__item {
  border: 2px solid var(--main-blue);
  border-radius: 15px;
  padding: 1.375rem 1.5rem 1.3125rem 1.3125rem;
}
@media screen and (min-width: 768px) {
  .faq__item {
    padding: 1.375rem 1.875rem 1.75rem 1.875rem;
    border-radius: 20px;
  }
}

@media (max-width: 767px) {
  .faq__item:nth-of-type(n+10) .faq__item-q-label {
    transform: translateX(-0.625rem);
  }
}

@media (max-width: 767px) {
  .faq__item--table {
    padding-left: 0.8125rem;
  }
  .faq__item--table .faq__item-question, .faq__item--table .faq__item-answer-inner {
    padding-left: 0.5rem;
  }
}

.faq__item-inner {
  display: flex;
  flex-direction: column;
}

.faq__item-header {
  display: grid;
  grid-template-columns: 1fr max-content;
  gap: 0.125rem;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .faq__item-header {
    gap: 0.625rem;
    padding-right: 1.5625rem;
  }
}
@media (any-hover: hover) {
  .faq__item-header:hover .faq__item-q-text {
    color: var(--main-blue);
  }
}

.faq__item-question {
  display: grid;
  grid-template-columns: 2.25rem 1fr;
  gap: 0.625rem;
}
@media screen and (min-width: 768px) {
  .faq__item-question {
    grid-template-columns: 4.4375rem 1fr;
    gap: 1.25rem;
    align-items: center;
  }
}

.faq__item-q-label {
  font-family: var(--font-family-second);
  font-size: 1.6rem;
  font-weight: var(--font-weight-black);
  letter-spacing: 0.06em;
  color: var(--main-blue);
  text-align: left;
  white-space: nowrap;
}
@media (max-width: 767px) {
  .faq__item-q-label {
    line-height: 1;
  }
}
@media screen and (min-width: 768px) {
  .faq__item-q-label {
    font-size: 2.5625rem;
  }
}

.faq__item-q-text {
  text-align: left;
  font-family: var(--font-family-second);
  font-size: 0.75rem;
  letter-spacing: 0.02em;
  font-weight: var(--font-weight-black);
  transition: color var(--duration-base) ease;
}
@media (max-width: 767px) {
  .faq__item-q-text {
    line-height: 1.4;
    display: flex;
    align-items: center;
  }
}
@media screen and (min-width: 768px) {
  .faq__item-q-text {
    font-size: 1.125rem;
    letter-spacing: 0.04em;
  }
}

.faq__item-toggle {
  aspect-ratio: 1/1;
  width: 0.875rem;
  border-radius: calc(infinity * 1px);
  border: 1px solid var(--black);
  position: relative;
  margin-top: 0.3125rem;
}
@media screen and (min-width: 768px) {
  .faq__item-toggle {
    width: 1.625rem;
    border: 2px solid var(--black);
    margin-top: 1.4375rem;
  }
}

.faq__item-toggle::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  width: 0.375rem;
  height: 1px;
  background-color: var(--black);
}
@media screen and (min-width: 768px) {
  .faq__item-toggle::before {
    width: 0.6875rem;
    height: 2px;
  }
}

.faq__item-toggle::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  width: 1px;
  height: 0.375rem;
  background-color: var(--black);
  transition: rotate var(--duration-base) ease;
}
@media screen and (min-width: 768px) {
  .faq__item-toggle::after {
    width: 2px;
    height: 0.6875rem;
  }
}

.faq__item-toggle.is-open::after {
  rotate: 90deg;
}

.faq__item-answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height var(--duration-base) ease;
}

.faq__item-answer-inner {
  padding-top: 0.8125rem;
  display: grid;
  grid-template-columns: 2.25rem 1fr;
  gap: 0.625rem;
}
@media screen and (min-width: 768px) {
  .faq__item-answer-inner {
    padding-top: 1.25rem;
    grid-template-columns: 4.4375rem 1fr;
    gap: 1.25rem;
  }
}

@media screen and (min-width: 768px) {
  .faq__item-answer-content {
    display: grid;
    grid-template-columns: 4.4375rem 1fr;
    gap: 1.25rem;
    margin-top: -0.875rem;
  }
}

@media (max-width: 767px) {
  .faq__item-answer-content-spacer {
    display: none;
  }
}

.faq__item-a-label {
  font-family: var(--font-family-second);
  font-size: 1.1875rem;
  font-weight: var(--font-weight-black);
  color: var(--white);
  text-align: left;
  background-color: var(--main-red);
  aspect-ratio: 1/1;
  width: 2.1875rem;
  border-radius: calc(infinity * 1px);
  text-align: center;
  line-height: 1.875rem;
}
@media screen and (min-width: 768px) {
  .faq__item-a-label {
    font-size: 1.9375rem;
    width: 3.3125rem;
    line-height: 2.875rem;
  }
}

.faq__item-a-text {
  font-size: 0.625rem;
  font-weight: var(--font-weight-semibold);
  line-height: 1.55;
}
@media screen and (min-width: 768px) {
  .faq__item-a-text {
    line-height: 1.7;
    font-size: 0.9375rem;
  }
}

@media screen and (min-width: 768px) {
  .faq__item-a-text--table {
    margin-top: 1rem;
  }
}

.faq__item-answer-content {
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .faq__item-answer-content-block {
    overflow: hidden;
  }
}

.faq__table-wrap {
  margin-top: 1.0625rem;
  width: 100%;
  overflow-x: auto;
}
@media screen and (min-width: 768px) {
  .faq__table-wrap {
    margin-top: 1.5rem;
  }
}

.faq__table {
  width: 100%;
  border-collapse: collapse;
  min-width: 495px;
}
@media screen and (min-width: 768px) {
  .faq__table {
    min-width: 43.75rem;
  }
}

.faq__table th,
.faq__table td {
  border: 1px solid #808080;
  padding: 0.215625rem 0.5rem;
}
@media screen and (min-width: 768px) {
  .faq__table th,
  .faq__table td {
    padding: 0.28125rem 0.625rem;
  }
}

.faq__table-head {
  color: var(--white);
  font-weight: var(--font-weight-bold);
  text-align: left;
  font-size: 0.875rem;
  width: 6.71875rem;
}
@media screen and (min-width: 768px) {
  .faq__table-head {
    font-size: 1.125rem;
    width: 9.375rem;
  }
}

.faq__table th.faq__table-head {
  padding-left: 1.25rem;
}
@media screen and (min-width: 768px) {
  .faq__table th.faq__table-head {
    padding-left: 1.625rem;
  }
}

.faq__table-head--blue {
  background-color: var(--main-blue);
}

.faq__table-head--red {
  background-color: var(--main-red);
}

.faq__table-head-text {
  font-size: 0.625rem;
  margin-top: 0.4375rem;
  font-feature-settings: initial;
}
@media screen and (min-width: 768px) {
  .faq__table-head-text {
    margin-top: 0.625rem;
    font-size: 0.875rem;
  }
}

.faq__table-head-text--conversation {
  font-feature-settings: "palt";
}
@media (max-width: 767px) {
  .faq__table-head-text--conversation {
    margin-top: 0;
